<template>
  <section>
    <div v-if="!parsed" class="bmt__import">
      <textarea class="bmt__import__textarea" wrap="physical" v-model="team" placeholder="Paste the exported text from Pokemon Showdown here"></textarea>
      <button @click="parseIt()">parse it</button>
    </div>
    <TeamView v-else :parsed='parsed.pokemon' />
  </section>
</template>


<script>
import axios from "axios";
const TeamView = () => import("@/components/TeamView.vue");
const parseTeam = () => import("@/services/teamParser");

export default {
  components: {
    TeamView: TeamView
  },
  data() {
    return {
      team: "",
      parsed: false,
      cnc: {}
    };
  },
  methods: {
    parseIt() {
      parseTeam().then(res => {
        let parsed = {};
        parsed.pokemon = res.parseTeam(this.team);
        parsed.tier = "ou";
        axios.post("https://dogars-api.herokuapp.com/bmt", parsed).then(res => {
          this.parsed = res.data;
        });
      });
    },
    checksAndCounters() {
      this.cnc = this.parsed.weak;
    }
  }
};
</script>